body{
  background-image: url(./img/bg-2.png);
}
.App h1 {
  text-align: center;
  color: #fff;
}
.App{
  background-color: transparent;
  min-height: 600px;
  width: 500px;
  margin: 40px auto;
}

 #main input{
   background-color: rgb(27,112,137);
   border: 0;
   width: 300px;
   height: 50px;
   padding: 0 20px;
   margin: 20px;
   font-size: 18px;
   border-radius: 10px;
   color: white;
 }
 #main input::placeholder{
   color: rgba(255,255,255,0.5);
 }

 #main input:focus, #main button:focus{
   outline: none;
 }

 #main button{
   height: 50px;
   width: 80px;
   border-radius: 5px;
   border: 0;
   background-color: rgb(202,156,24);
   cursor: pointer;
   font-weight: bolder;
   color: #fff;
 }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
